<template>
    <div class="content">
        <div class="alert_box">
            <div class="investor-box">
                <!-- <img src="../../images/homepage_txsc.png" alt=""> -->
                <img :src="src" alt="">
                <span class="investor-name">{{name}}</span>
                <span>邀请你加入</span>
            </div>
            <div class="company_info_box">
                <h1>{{mechanism_name}}</h1>
                <h3>已经用搜股XPE进行工作沟通，请尽快加入</h3>
                <div class="line-box line-box-phone ">
                    <!-- <el-select v-model="value" placeholder="请选择" class="header-right-time-phone">
                                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                                        </el-option>
                                    </el-select> -->
                    <input type="number" placeholder="输入手机号" class="phone-input-text cell" maxlength="11" v-model="phoneValue" @focus="toBlur">
                    <span class="error" v-if="phoneError1">{{phoneErrorMessage}}</span>
                </div>
                <div class="line-box yzmCode">
                    <input type="number" placeholder="手机验证码" class="phone-input-text " maxlength="6" v-model="phoneCode">
                    <div class="yzm-btn" :class="{'btn-blue':!phoneError&&hasgetCode}" @click="toGetPhoneCode">{{getPhoneCodeText}}</div>
                    <span class="error" v-if="yzmError">{{yzmErrorMessage}}</span>
                </div>
                <div class="line-box ">
                    <input type="text" placeholder="真实姓名" class="phone-input-text " v-model="trueName">
                    <span class="error" v-if="trueNameError">{{trueNameErrorMessage}}</span>
                </div>
                <div class="line-box">
                    <input type="text" placeholder="申请理由(选填)" class="phone-input-text " v-model="applyReason">
                </div>
                <div class="line-box">
                		<!--<span class="phone-input-text">{{ investCode }}</span>-->
                    <input type="text" placeholder="团队邀请码" class="phone-input-text " maxlength="6" v-model="investCode" disabled>
                    <span class="error" v-if="yqmError">{{yqmErrorMessage}}</span>
                </div>
                <div class="join-btn" @click="toCommit">
                    加入该组织
                </div>
                <h3 class="ts">所有未注册用户将自动注册</h3>
            </div>
    
        </div>
        <div class="content_bottom">
        		<span>搜股XPE</span>
            <div></div>
            <span>搜股科技旗下办公平台</span>
        </div>
    </div>
</template>

<script>
    import {
        sendSaasCode,
        approveJoin,
        inviteInfo
    } from '../../config/api'
    import {
        dialogConfirm,
        dialogConfirmHasbtn
    } from '../../config/mUtils'
    export default {
        data() {
            return {
                value: '',
                options: [{
                    value: '+86',
                    label: '+86'
                }],
                phoneValue: '',
                phoneCode: '',
                trueName: '',
                applyReason: '',
                investCode: '',
                phoneErrorMessage: '',
                yzmErrorMessage: '',
                yqmErrorMessage: '',
                trueNameErrorMessage: '',
                toSave: false,
                hasgetCode: true,
                getPhoneCodeText: '获取验证码',
                phoneError1: false,
                mechanism_name: '',
                name: '',
                src: '../../../static/images/header_userimg.png',
            }
        },
        components: {
    
        },
        created() {
            if (this.$route.query.userId != undefined) {
                this.getInviteInfo(this.$route.query.userId);
                this.investCode = this.$route.query.code;
            }
        },
        mounted() {},
        computed: {
            phoneError: function() {
                if (this.phoneValue == '') {
                    return true
                } else {
                    if (!(/^[1][3,4,5,7,8][0-9]{9}$/gi.test(this.phoneValue))) {
                        return true
                    } else {
                        return false;
                    }
                }
            },
            trueNameError: function() {
                if (this.toSave) {
                    if (this.trueName == '') {
                        this.trueNameErrorMessage = '姓名不能为空';
                        return true
                    } else {
                        return false;
                    }
                } else {
                    return false;
                }
            },
            yzmError: function() {
                if (this.toSave) {
                    if (this.phoneCode == '') {
                        this.yzmErrorMessage = '验证码不能为空';
                        return true
                    } else {
                        return false;
                    }
                } else {
                    return false;
                }
            },
            yqmError: function() {
                if (this.toSave) {
                    if (this.investCode == '') {
                        this.yqmErrorMessage = '邀请码不能为空';
                        return true
                    } else {
                        return false;
                    }
                } else {
                    return false;
                }
            },
        },
        methods: {
            toBlur() {
                this.phoneError1 = false
            },
            //提交信息
            toCommit() {
                this.toSave = true;
                if (this.phoneValue == '') {
                    this.phoneErrorMessage = '手机号码不能为空';
                    this.phoneError1 = true;
                } else {
                    if (!(/^[1][3,4,5,7,8][0-9]{9}$/gi.test(this.phoneValue))) {
                        this.phoneErrorMessage = '手机号码错误';
                        this.phoneError1 = true;
                    } else {
                        this.phoneError1 = false;
                    }
                }
                if (!this.yzmError && !this.yqmError && !this.trueNameError) {
                    approveJoin({
                        phone: this.phoneValue,
                        code: this.phoneCode,
                        name: this.trueName,
                        inv_code: this.investCode,
                    }).then(res => {
                        if (res.data.msgNo == 1) {
                            dialogConfirm('success', '加入成功');
                            // console.log(res);
    
                            // let appstore, ua = navigator.userAgent;
                            // if (ua.match(/Android/i)) {
                            //     appstore = 'market://search?q=com.singtel.travelbuddy.android';
                            // }
                            // if (ua.match(/iphone|ipod|ipad/)) {
                            //     appstore = "https://itunes.apple.com/cn/app/wei-xin/id414478124?mt=8&ign-mpt=uo%3D4";
                            // }
                            // this.applink(appstore)
                            // if (navigator.userAgent.match(/(iPhone|iPod|iPad);?/i)) {
                            //     var loadDateTime = new Date();
                            //     window.setTimeout(function() {
                            //         var timeOutDateTime = newDate();
                            //         if (timeOutDateTime - loadDateTime < 5000) {
                            //             window.location = "要跳转的页面URL";
                            //         } else {
                            //             window.close();
                            //         }
                            //     },25);
                            //     window.location = " apps custom url schemes";
                            // } else if (navigator.userAgent.match(/android/i)) {
                            //     var state = null;
                            //     try {
                            //         state = window.open("apps custom url schemes ", '_blank');
                            //     } catch (e) {}
                            //     if (state) {
                            //         window.close();
                            //     } else {
                            //         window.location = "要跳转的页面URL";
                            //     }
                            // }
    
                        } else {
                            dialogConfirm('info', res.data.message.split('错误码')[0]);
                        }
                    })
                }
    
            },
    
            //获取验证码
            toGetPhoneCode() {
                if (!this.phoneError && this.hasgetCode) {
                    sendSaasCode({
                        phone: this.phoneValue
                    }).then(res => {
                        if (res.data.msgNo == 1) {
                            dialogConfirm('success', '发送成功');
                            var that = this;
                            this.hasgetCode = false;
                            var num = 60;
                            var timer = setInterval(function() {
                                num--;
                                that.getPhoneCodeText = num + '秒后重新获取';
                                if (num === 0) {
                                    that.getPhoneCodeText = '获取验证码';
                                    that.hasgetCode = true;
                                    clearInterval(timer);
                                }
                            }, 1000)
                        } else {
                            dialogConfirm('info', res.data.message.split('错误码')[0]);
                        }
                    })
                }
            },
            getInviteInfo(id) {
                inviteInfo({
                    user_id: id
                }).then(res => {
                    if (res.data.msgNo == 1) {
                        console.log(res);
                        let data = res.data.payload;
                        this.mechanism_name = data.mechanism_name;
                        this.name = data.name;
                        if (data.url != '') {
                            this.src = data.url;
                        }
                    } else {
                        dialogConfirm('info', res.data.message.split('错误码')[0]);
                    }
                })
            },
            // applink(fail) {   
            //     return function() {    
            //         var clickedAt = +new Date;    
            //         setTimeout(function() {      
            //             if (+new Date - clickedAt < 2000) {       
            //                 window.location = fail;      
            //             }    
            //         }, 500);    
            //     };  
            // },
        }
    }
</script>

<style lang="scss" scoped>
    html {
        overflow-y: scroll;
    }
    
     :root {
        overflow-y: auto;
        overflow-x: hidden;
    }
    
     :root body {
        position: absolute;
    }
    
    body {
        width: 100vw;
        overflow: hidden;
    }
    
    .content {
        background-color: #F7F9FC;
        padding: 0.4rem 0.26666666666666666rem 0rem;
        position: absolute;
        top: 0px;
        bottom: 0px;
        left: 0px;
        right: 0px;
        overflow: auto;
        .alert_box {
            background-color: #ffffff;
            border-radius: 0.16rem;
            padding-bottom: 0.3466666666666667rem;
        }
        .content_bottom {
            display: flex;
            align-items: center;
            justify-content: center;
            padding-top: 0.48rem;
            padding-bottom: 0.5333333333333333rem;
            img {
                width: 1.2rem;
                height: 0.32rem;
            }
            div {
                border-right: 1px solid #C1C1C1;
                margin: 0 0.29333333333333333rem;
                height: 0.32rem;
            }
            span {
                font-size: 0.32rem;
                color: #C1C1C1;
            }
        }
    }
    
    .investor-box {
        display: flex;
        align-items: center;
        padding: 0.32rem 0.37333333333333335rem;
        border-bottom: 1px solid #f1f1f1;
        margin-bottom: 0.9066666666666666rem;
        &>span {
            font-size: 0.37333333333333335rem;
        }
        .investor-name {
            color: #282828;
            padding: 0px 0.10666666666666667rem 0 0.21333333333333335rem;
        }
        &>span:nth-child(3) {
            color: #808080;
        }
        img {
            width: 0.5866666666666667rem;
            height: 0.5866666666666667rem;
            border-radius: 50%;
        }
    }
    
    .company_info_box {
        padding: 0px 0.8rem;
        h1,
        h3 {
            text-align: center;
        }
        h1 {
            font-size: 0.6133333333333333rem;
            font-weight: bold;
        }
        h3 {
            font-size: 0.37333333333333335rem;
            color: #A0A4AA;
            margin-bottom: 2rem;
        }
        .line-box {
            border-bottom: 1px solid #F1F1F1;
            width: 100%;
            position: relative;
            padding-bottom: 0.21333333333333335rem;
            margin-bottom: 0.8rem;
            display: flex;
            align-items: center;
            .error {
                position: absolute;
                right: 0px;
                color: #F01400;
                font-size: 12px;
                // top: -10px
            }
        }
        .line-box-phone {
            margin-bottom: 0.4266666666666667rem;
            padding-bottom: 0rem;
        }
        .yzmCode {
            width: calc(100% - 130px);
            height: 36px;
            line-height: 36px;
            position: relative;
            padding-bottom: 0px;
            .error{
                left: 0px;
                top: 30px;
            }
        }
        .join-btn {
            width: 100%;
            background: url('../../images/icon_json_phone.png') center no-repeat;
            background-size: 100%;
            text-align: center;
            padding: 0.32rem 0px;
            font-size: 0.48rem;
            color: #ffffff;
            cursor: pointer;
        }
        .ts {
            color: #819BB5;
            margin-top: 0.16rem;
            margin-bottom: 0px;
            font-size: 0.32rem;
        }
    }
    
    .header-right-time-phone {
        width: 70px;
        height: 100%;
    }
    
    .phone-input-text {
        font-size: 0.4266666666666667rem;
        color: #282828;
        width: 100%;
        background: white;
    }
    
    .cell {
        width: calc(100% - 2.6666666666666665rem);
        padding-bottom: 0.21333333333333335rem;
        // border-left: 1px solid #F1F1F1;
        // text-indent: 0.26666666666666666rem;
    }
    
    .yzm-btn {
        display: inline-block;
        width: 115px;
        font-size: 14px;
        height: 36px;
        line-height: 36px;
        color: #ffffff;
        text-align: center;
        background-color: #D9D9D9;
        position: absolute;
        right: -130px;
        top: 0rem;
        cursor: pointer;
    }
    
    .btn-blue {
        background-color: #4390F0;
    }
</style>



